// Reusable TextField with small label
const CustomTextField = ({ hide = false, ...rest }) => (
  <TextField
    variant="outlined"
    size="small"
    InputLabelProps={{
      shrink: true,
      style: { fontSize: "16px" },
    }}
    sx={{
      display: hide ? 'none' : 'block',
      "& .MuiOutlinedInput-root": {
        "&:hover fieldset": { borderColor: "var(--bs-primary1)" },
        "&.Mui-focused fieldset": { borderColor: "var(--bs-primary1)" },
      },
    }}
    {...rest}
  />
);



const CustomOrderCheckOutBox = ({title , children})=> 
  <Paper
  sx={{
    padding: 3,
    borderRadius: 2,
    border: "1px solid #ccc",
    marginTop: 4,
    // background: "linear-gradient(135deg,  #F87A1F ,#FFCC29)",
    boxShadow: "0 4px 12px rgba(0, 0, 0, 0.1)",
  }}
>
    <Box sx={{
      px:2,
      py:1

    }}>
     {title &&
        <Typography
    variant="h6"
    gutterBottom
    sx={{
      paddingBottom: 2,
      fontWeight: "600",
      fontSize: "18px",
      color: "var(--bs-primary1)",
    }}
  >
    {title}
  </Typography> }

  <Box   display="flex"
        sx={{
          flexDirection: {
            xs: "column",
            sm: "row",
          },
          gap: 2,
          flexWrap:'nowrap'
        }}>
          {children}
 
  </Box>
  
    </Box>



</Paper>

const CustomOrderBox = ({title , children})=> 
  <Paper
  sx={{
    padding: 3,
    borderRadius: 2,
    border: "1px solid #ccc",
    marginTop: 4,
    
    // background: "linear-gradient(135deg,  #F87A1F ,#FFCC29)",
    boxShadow: "0 4px 12px rgba(0, 0, 0, 0.1)",
  }}
>
    <Box sx={{
      px:2,
      py:1

    }}>
     {title &&
        <Typography
    variant="h6"
    gutterBottom
    sx={{
      paddingBottom: 2,
      fontWeight: "600",
      fontSize: "18px",
      color: "var(--bs-primary2)",
    }}
  >
    {title}
  </Typography> }

  <Box >
          {children}
 
  </Box>
  
    </Box>



</Paper>

const InfoBlock = ({ title, value }) => {
  return (
    <Box flex={1}>
      <Typography
        variant="h6"
        gutterBottom
        sx={{
          // fontWeight: "600",
          fontSize: "14px",
          color: "white",
        }}
      >
        {title}
      </Typography>
      <Typography
        variant="h6"
        gutterBottom
        sx={{
          // fontWeight: "600",
          fontSize: "14px",
          color: "white",
        }}
      >
        {value}
      </Typography>
    </Box>
  );
};

const OrderInfoBlock = ({ title, value }) => {
  return (
    <Box
      display="flex"
      justifyContent="space-between"
      width="100%"
    >
      <Typography
        variant="h6"
        gutterBottom
        sx={{
          fontSize: "14px",
          color: "var(--bs-nav)",
        }}
      >
        {title}
      </Typography>

      <Typography
        variant="h6"
        gutterBottom
        sx={{
          fontSize: "14px",
          color: "var(--bs-nav)",
          opacity: 0.8,
        }}
      >
        {value}
      </Typography>
    </Box>
  );
};







const AddWareHouse = ({setCheckWareHouse}) => {
  const [warehouseCreationCompelete , setWareHouseCreationComplete] = useState(false)
  const  { setWarehouseDetails }= useCourierDashboard()

  const warehouseFind = async()=> {
    try {
     let response = await getWareHouse()
    
     if(response?.data?.statusCode === 200 )
     {
      setWarehouseDetails(response?.data?.data)
      setWareHouseCreationComplete(true)
      setCheckWareHouse(true)
     }
    } catch (error) {
     
    } finally {
    }
}
  const [formData, setFormData] = useState({
    name: "",
    city: "",
    state: "",
    pincode: "",
    address: "",
  });

  // Fetch API data after name is entered
  const fetchWarehouseData = async (value) => {

    let response  = await createWareHouse({
      warehouseName:value.name,
      business_Address : value?.address
  })
  

      if(!response?.data?.error)
      {
        let res = await warehouseFind()
       
      
      }
      toastMessage(response?.data?.error , response?.data?.message)
  };

  const findWarehouse = async ()=>{

    try {
      let response = await getCourierWareHouseInformation()
      if(response?.data?.error   === false)
      {
        setFormData((pre)=>({...pre ,
          city: response?.data?.data?.business_city,
          state: response?.data?.data?.business_state,
          pincode:  response?.data?.data?.business_pincode,
         address: response?.data?.data?.business_Address, 
    }))
      }
    } catch (error) {
      
    }
  
  }

  useEffect(()=>{
    findWarehouse() 
  },[])



  const validationSchema = Yup.object({
    name: Yup.string().required("Warehouse name is required"),
    address: Yup.string().required("Warehouse address is required"),

  });

  return (
    <Container maxWidth="lg" style={{ marginTop: "2rem" }}>
      <Grid container spacing={4}>
        <Grid item xs={12}>
          <Card style={{ padding: "2rem", boxShadow: "0px 4px 10px rgba(0,0,0,0.1)" }}>
            <Typography variant="h5" align="center" gutterBottom>
              Create a Warehouse
            </Typography>
            <Formik
              initialValues={formData}
              enableReinitialize
              validationSchema={validationSchema}
              onSubmit={async (values, { setSubmitting }) => {
              
                await fetchWarehouseData(values);
                setSubmitting(false);
                
              }}
            >
              {({ getFieldProps, touched, errors, values, setFieldValue , isSubmitting }) => (
                <Form>
                  <Grid container spacing={2}>
                    {/* Name Field (Editable) */}
                    <Grid item xs={6}>
                      <TextField
                        label="Name"
                        variant="outlined"
                        fullWidth
                        margin="normal"
                        {...getFieldProps("name")}
                        error={touched.name && Boolean(errors.name)}
                        helperText={touched.name && errors.name}
                        // onBlur={(e) => {
                        //   if (e.target.value) fetchWarehouseData(e.target.value);
                        // }}
                        sx={{
                          "& .MuiOutlinedInput-root": {
                            "&:hover fieldset": { borderColor: "var(--bs-primary1)" },
                            "&.Mui-focused fieldset": { borderColor: "var(--bs-primary1)" },
                          },
                        }}
                      />
                    </Grid>
                    <Grid item xs={6}>
                      <TextField
                        label="Address"
                        variant="outlined"
                        fullWidth
                        margin="normal"
                        {...getFieldProps("address")}
                        error={touched.address && Boolean(errors.address)}
                        helperText={touched.address && errors.address}
                        // onBlur={(e) => {
                        //   if (e.target.value) fetchWarehouseData(e.target.value);
                        // }}
                        sx={{
                          "& .MuiOutlinedInput-root": {
                            "&:hover fieldset": { borderColor: "var(--bs-primary1)" },
                            "&.Mui-focused fieldset": { borderColor: "var(--bs-primary1)" },
                          },
                        }}
                      />
                   
                    </Grid>

                    {/* Other Fields (Disabled) */}
                    {Object.keys(formData)
  .filter((field) => !["name", "address"].includes(field))
  .map((field) => (
    <Grid item xs={6} key={field}>
      <TextField
         label={field.charAt(0).toUpperCase() + field.slice(1)}
        {...getFieldProps(field)}
        error={touched[field] && Boolean(errors[field])}
        helperText={touched[field] && errors[field]}
        disabled
        variant="outlined"
        fullWidth
        margin="normal"
        sx={{
          "& .MuiOutlinedInput-root": {
            "&:hover fieldset": { borderColor: "var(--bs-primary1)" },
            "&.Mui-focused fieldset": { borderColor: "var(--bs-primary1)" },
          },
        }}
      />
    </Grid>
  ))}


                  </Grid>
                  <Typography
    variant="caption"
    color="textSecondary"
    sx={{ marginLeft: "0px", marginTop: "-4px" }}
  > Address Format : Shop No, Street/Colony, Landmark , City, Pincode ( ,      ,  ,   , )
  </Typography>

                  <Box
  display="flex"
  flexDirection={{ xs: "column", sm: "row" }} // Column on small screens, row on larger screens
  gap={2} // Adds spacing between buttons
  justifyContent="center" // Centers buttons
  alignItems="center"
  mt={3} // Adds top margin
>
  <Button
    type="submit"
    variant="outlined"
    color="primary"
    disabled={warehouseCreationCompelete || isSubmitting}
    sx={{
      width: { xs: "100%", sm: "200px" }, // Full width on mobile, fixed width on larger screens
      borderColor: "var(--bs-primary1)",
      color: "var(--bs-primary1)",
    }}
  >
    {isSubmitting ? 'Creating....' : 'Create Warehouse'} 

  </Button>

 
</Box>

                </Form>
              )}
            </Formik>
          </Card>
        </Grid>
      </Grid>
    </Container>
  );
};

