        {tabIndex === 2 && <CourierAgency onComplete={handleComplete} />}
        {tabIndex === 3 && <ProductDescription onComplete={handleComplete} />}
        {tabIndex === 4 && <SenderDetails onComplete={handleComplete} />}
        {tabIndex === 5 && <ReceiverDetails onComplete={handleComplete} />}
        {tabIndex === 6 && <ReviewAndConfirm onComplete={handleComplete} />}
        {tabIndex === 7 && <BookingPayment onComplete={handleComplete} />}
        {tabIndex === 8 && <GenerateChallan />}
