const UploadBusinessImages = ({ onComplete }) => {
    // Initialize all image upload hooks individually at the top of the component
    const imageUpload1 = useCourierImageUploadOnboard();
    const imageUpload2 = useCourierImageUploadOnboard();
    const imageUpload3 = useCourierImageUploadOnboard();
    const imageUpload4 = useCourierImageUploadOnboard();
    const imageUpload5 = useCourierImageUploadOnboard();
    const imageUpload6 = useCourierImageUploadOnboard();
    const imageUpload7 = useCourierImageUploadOnboard();
    const imageUpload8 = useCourierImageUploadOnboard();
    const imageUpload9 = useCourierImageUploadOnboard();
    const imageUpload10 = useCourierImageUploadOnboard();
  
    const [error, setError] = useState("");
  
    // Array of all image upload hooks
    const imageUploads = [
      imageUpload1,
      imageUpload2,
      imageUpload3,
      imageUpload4,
      imageUpload5,
      imageUpload6,
      imageUpload7,
      imageUpload8,
      imageUpload9,
      imageUpload10,
    ];
  
    const titles = [
      "Shop Inner Image",
      "Shop Outer Image",
      "Shop Left of Outer",
      "Shop Right of Outer",
      "Shop Front",
      "Shop Nearby Image",
      "Aadhaar Front Image",
      "Aadhaar Back Image",
      "Pancard Image",
      "Bank Proof Image",
    ];
  
    const getDoc = async()=>{
      
      try {
       let res = await getCorieronboardingDoc()
       if(res?.data?.error === false)
        
       {
   
        imageUploads[0].setImageUrl(res?.data?.data?.shopInnerImg)
        imageUploads[1].setImageUrl(res?.data?.data?.shopOuterImg)
        imageUploads[2].setImageUrl(res?.data?.data?.shopLeftOfOuter)
        imageUploads[3].setImageUrl(res?.data?.data?.shopRightOfOuter)
        imageUploads[4].setImageUrl(res?.data?.data?.shopFront)
        imageUploads[5].setImageUrl(res?.data?.data?.shopNearByImage)
        imageUploads[6].setImageUrl(res?.data?.data?.adhaarImgFront)
        imageUploads[7].setImageUrl(res?.data?.data?.adhaarImgBack)
        imageUploads[8].setImageUrl(res?.data?.data?.panImg)
        imageUploads[9].setImageUrl(res?.data?.data?.bankImg)
       }

      } catch (error) {
       
      }
   }
    useEffect(()=>{
      getDoc()
    },[])

 
  
    const handleSubmit = async () => {
      // Check if any image URL is missing
      const missingImages = imageUploads.some(({ imageUrl }) => !imageUrl);
  
      if (missingImages) {
        setError("Please upload all images before proceeding.");
        return;
      }
  
      setError(""); // Clear error if all images are uploaded
  
      const body = {
        shopInnerImg: imageUploads[0].imageUrl,
        shopOuterImg: imageUploads[1].imageUrl,
        shopLeftOfOuter: imageUploads[2].imageUrl,
        shopRightOfOuter: imageUploads[3].imageUrl,
        shopFront: imageUploads[4].imageUrl,
        shopNearByImage: imageUploads[5].imageUrl,
        adhaarImgFront: imageUploads[6].imageUrl,
        adhaarImgBack: imageUploads[7].imageUrl,
        panImg: imageUploads[8].imageUrl,
        bankImg: imageUploads[9].imageUrl,
      };
  
      try {
        let res = await corieronboardingAddDoc(body);
        toastMessage(res?.data?.error, res?.data?.message);
        if (!res?.data?.error) {
          onComplete();
        }
      } catch (error) {
        console.error("Error submitting images:", error);
      }
    };


  
    return (
      <>
        <div className="container">
          <div className="row g-4">
            {imageUploads.map((upload, index) => (
              <div key={index} className="col-12 col-md-6 col-lg-4 col-xl-3">
                <upload.ImageUpload index={index} title={titles[index]} />
              </div>
            ))}
          </div>
        </div>
  
        {error && <p style={{ color: "red", textAlign: "center", marginTop: "10px" }}>{error}</p>}
  
        <Stack spacing={3} alignItems="center" marginTop={4}>
          <Button
            type="submit"
            variant="contained"
            color="primary"
            onClick={handleSubmit}
            sx={{
              borderColor: "var(--bs-primary1)",
              width: "200px",
              backgroundColor: "var(--bs-primary1)",
              color: "white",
            }}
          >
            Next
          </Button>
        </Stack>
      </>
    );

}



 const UploadBusinessVideo = ({onComplete}) => {
    const [showRecorder, setShowRecorder] = useState(false);
    const [recordedVideo, setRecordedVideo] = useState(null);
    const [isUploading, setIsUploading] = useState(false);
  
    const handleSaveVideo = (videoUrl) => {
      setRecordedVideo(videoUrl);
      setShowRecorder(false);
    };
  
    const uploadVideo = async (videoBlob) => {
      setIsUploading(true);
      const video = new FormData();
      video.append("video", videoBlob, "recorded_video.webm");
      video.append("user_id", window.localStorage.getItem("userIdToken"));
      console.log(video , "video")
      
      try {
        // Upload logic here
        // toastMessage(res?.data?.error , res?.data?.message)
      
       let res = await  corieronboardingKycVideo(video)
       toastMessage(res?.data?.error , res?.data?.message)
       if(!res?.data?.error)
       {
        onComplete()
       }
      } catch (error) {
      
      } finally {
        setIsUploading(false);
      }
    };
  
    const handleUpload = async () => {
      if (recordedVideo) {
        const videoBlob = await fetch(recordedVideo)?.then(r => r.blob());
        console.log(videoBlob , "videoBlob")
           await uploadVideo(videoBlob);
      } else {
        // toast.error("Please record a video first");
      }
    };
  
    return (
      <div className="container text-center">
        {/* <h6>Video KYC Recording</h6> */}
       
        {recordedVideo ? (
          <div className="mt-3">
            <video src={recordedVideo} controls style={{ width: '100%', maxWidth: '500px' }} />
            {/* <Button onClick={handleUpload} disabled={isUploading} className="mt-3">
              {isUploading ? 'Uploading...' : 'Upload Video'}
            </Button> */}

<Stack spacing={3} alignItems="center" marginTop={4}>
         <Button
           type="submit"
           variant="contained"
           color="primary"
         
           sx={{
             borderColor: 'var(--bs-primary1)',
             width: "200px",
             backgroundColor: 'var(--bs-primary1)',
             color: "white",
           }}
           onClick={handleUpload} disabled={isUploading}
         >
           Next
         </Button>
       </Stack>
          </div>
        ) : <>
         <Box
            sx={{
                position: 'relative',
                width: '100%',
                height: 350,
                borderRadius: 2,
                overflow: 'hidden',
                bgcolor: 'grey.200',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                // cursor: 'pointer',
            }}
           
        >
         
                <Button variant="outlined" color="textSecondary" onClick ={()=> setShowRecorder(true)}>{'Video KYC'}</Button>
        
         
        </Box>
        
        </>}
        <VideoRecorder 
          show={showRecorder} 
          handleClose={() => setShowRecorder(false)} 
          onSave={handleSaveVideo}
        />
        {/* <ToastContainer /> */}

        
      </div>
    );
  };

