const VerifyDocuments = ({setCurrentPage , onComplete}) => {
  // const pdfUrl = "https://filesamples.com/samples/document/pdf/sample1.pdf";
  const [pdfUrl , setPdfUrl] = useState('')
  
    const [open, setOpen] = useState(false);

    const handleSubmit = () => {
      setOpen(true); // Open the popup
    };
  
    const handleClose = async() => {
      let body = {
        is_courierAgreement:true
      }
      let res  = await corierAgrementCheck(body)
      if(res?.data?.error === false)
      {
        onComplete()
        setOpen(false); 
        setCurrentPage('onboarding-pending-page')
      }
      
      //
    };

    useEffect(() => {
      let fileURL;
      
      const getAggrement = async () => {
        try {
          let res = await getcorieronboardAggrement();
          
          setTimeout(()=>{
            fileURL = URL.createObjectURL(res.data);
            setPdfUrl(fileURL);
          },50)
         
        } catch (error) {
          console.error(error);
        }
      };
    
      getAggrement();
    
      return () => {
        if (fileURL) {
          URL.revokeObjectURL(fileURL);
        }
      };
    }, []);
    
    return (
        <Container maxWidth="sm" >
            <Card style={{ padding: "2rem", boxShadow: "0px 4px 10px rgba(0,0,0,0.1)" }}>

          
                <Typography variant="h5" align="center" gutterBottom>
                    Agreement
                </Typography>
                <iframe src={pdfUrl} width="100%" height="600px" />
                <Button variant="contained" color="success" fullWidth onClick={handleSubmit} style={{ marginTop: "1rem" }}  sx={{
             borderColor: 'var(--bs-primary1)',
             backgroundColor: 'var(--bs-primary1)',
             color: "white",
           }}>
                    Submit
                </Button>
            </Card>

            <Dialog open={open} onClose={handleClose}>
        <DialogTitle sx={{color:'var(--bs-primary1)'}} >Thank You!</DialogTitle>
        <DialogContent>
          <Typography sx={{color:'var(--bs-nav)'}}>
            Your application has been received, and our onboarding team will provide an update on its status soon.
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary" variant="contained" style={{ marginTop: "1rem" }}  sx={{
             borderColor: 'var(--bs-primary1)',
             backgroundColor: 'var(--bs-primary1)',
             color: "white"}}>
            Close
          </Button>
        </DialogActions>
      </Dialog>
        </Container>
    );
};


const PaymentGateWay = ({showSponsorTab , setPaymentComplete }) => {
    const [selectedPayment, setSelectedPayment] = useState(null);
    const [tpin, setTpin] = useState("");
    const [error, setError] = useState("");
    const [bal , setBal] = useState()
    const [loading , setLoading] = useState(false)
    const [firstLoading , setFirstLoading] = useState(true)

    useEffect(()=>{
       const getAmount = async()=> {
             setFirstLoading(true)
            try {
                let res = await getsponserPaymentAmount()
                    setBal(res?.data?.data?.amt)
                    if(res?.data?.data?.paymentStatus === true)
                    {
                        setPaymentComplete(true);
                    }
            } catch (error) {
                
            }
            finally{
                setFirstLoading(false)
            }
       }
       getAmount()
    },[])

    const handleWalletPayment = withLocation(async (latitude, longitude) => {
        if (tpin.length !== 4) {
            setError("Enter a valid 4-digit TPIN");
            return;
        }
        setError("");
        let body = {
            tpin:tpin,
            latitude,
            longitude,
                
        };
        setLoading(true)
        try {
            let res = await sponserPaymentWallet(body);
             toastMessage(res?.data?.error , res?.data?.message)
             if(!res?.data?.error)
             {
                
                // setOtpSent(true);
                setPaymentComplete(true);
             }
            //  setOtpSent(true);
            //  setSponserUser(res?.data?.data?.sponsorData)
            //  setOtp('')
            
        } catch (error) {
            console.error("Error sending OTP:", error);
        } finally{
            setLoading(false)
        }
    });

    const loadGateWay = async(body)=>{
        console.log(body , "body")
        const isLoaded = await loadScript("https://checkout.razorpay.com/v1/checkout.js");

        if (!isLoaded) {
            alert("Razorpay SDK failed to load. Please check your internet connection.");
            return;
        }
       
        const options = {
            key: razor_pay_key, 
            amount: body.amount,  
            currency: "INR",
            name: "Needu",
            description: "Payment for Onboarding",
            order_id : body?.id, 
            handler: async function (response) {
              
               
                let res  =  await verifyponserPaymentGatewayResponse(response)
                toastMessage(res?.data?.error , res?.data?.message)
                setLoading(false)
                if(!res?.data?.error)
                {
                   
                    setPaymentComplete(true); 
                }
               
            },
            prefill: {
                name: "User Name",
                email: "user@example.com",
                contact: "9999999999",
            },
            theme: {
                color: "#3399cc",
            },
        };

        const paymentObject = new window.Razorpay(options);
        paymentObject.on('payment.failed', async function (response){
            let res  =  await verifyponserPaymentGatewayResponse(response?.error)
            toastMessage(res?.data?.error , res?.data?.message)
            setLoading(false)
            
    });
        paymentObject.open();
    }

    const handleRazorpayPayment = withLocation( async (latitude, longitude) => {
        setLoading(true)
           let body = {
            latitude ,
            longitude
           }
         
          try {
            let res = await createsponserPaymentGatewayOrder(body)
          
              if(!res.error)
              {
                loadGateWay(res?.data?.data)
              }
          } catch (error) {
            
          }

       
    })

      
    if(firstLoading)
    {
        return (
            <></>
        )
    }

    
    return (
        <Container style={{ marginTop: "2rem", display: "flex", justifyContent: "center" }}>
            <Card
                style={{
                    padding: "2rem",
                    maxWidth: "500px",
                    width: "100%",
                    boxShadow: "0px 4px 15px rgba(0,0,0,0.1)",
                    borderRadius: "10px",
                }}
            >
                
                <Typography variant="h5" align="center" gutterBottom style={{color:'var(--bs-primary1)'}}>
                    Select Payment Method
                </Typography>
                <Typography variant='body1' align="center" gutterBottom style={{color:'var(--bs-primary1)'}}>
                    You Have Choosen to Start {showSponsorTab ? 'With Sponsor' : 'Without Sponsor'}
                </Typography>
                <FormControl component="fieldset" style={{ width: "100%", marginBottom: "1rem" }}>
                    <FormLabel component="legend" style={{ fontWeight: "bold", marginBottom: "0.5rem" ,color:'var(--bs-primary1)' }}>
                        Choose an option:
                    </FormLabel>
                    <RadioGroup
                        aria-label="payment-method"
                        name="payment-method"
                        value={selectedPayment}
                        onChange={(e) => {
                            setSelectedPayment(e.target.value);
                            setTpin("");
                            setError("");
                        }}
                    >
                        <FormControlLabel
                            value="gateway"
                            control={<Radio color="primary" />}
                            label="Payment Gateway"
                            style={{
                                backgroundColor: selectedPayment === "gateway" ? "#f0f0f0" : "transparent",
                                borderRadius: "8px",
                                padding: "0.5rem",
                                transition: "0.3s",
                            }}
                        />
                        <FormControlLabel
                            value="wallet"
                            control={<Radio color="secondary" />}
                            label="Wallet"
                            style={{
                                backgroundColor: selectedPayment === "wallet" ? "#f0f0f0" : "transparent",
                                borderRadius: "8px",
                                padding: "0.5rem",
                                transition: "0.3s",
                            }}
                        />
                    </RadioGroup>
                </FormControl>

                {selectedPayment && (
                    <>
                        <Typography variant="h6" align="center" style={{ marginTop: "1rem" ,color:'var(--bs-primary1)' }} >
                            Confirm Payment
                        </Typography>

                        {selectedPayment === "wallet" && (
                            <TextField
                                label="Enter 4-digit TPIN"
                                variant="outlined"
                                fullWidth
                                margin="normal"
                                type="password"
                                value={tpin}
                                onChange={(e) => setTpin(e.target.value.replace(/\D/g, "").slice(0, 4))}
                                error={!!error}
                                helperText={error}
                                required
                                sx={{
                                    "& .MuiOutlinedInput-root": {
                                      "&:hover fieldset": { borderColor: "var(--bs-primary1)" }, // Hover border
                                      "&.Mui-focused fieldset": { borderColor: "var(--bs-primary1)" }, // Focus border
                                    },
                                  }}
                            />
                        )}

                        <Button
                            onClick={selectedPayment === "wallet" ? handleWalletPayment : handleRazorpayPayment}
                            variant="contained"
                            
                            color="primary"
                            fullWidth
                            disabled={loading}
                            sx={{
                                borderColor: 'var(--bs-primary1)', // Custom colors
                              
                                backgroundColor: 'var(--bs-primary1)', // Custom colors
                                color: "white",
                              }}
                            style={{
                                marginTop: "1rem",
                                padding: "0.8rem",
                                fontSize: "1rem",
                                fontWeight: "bold",
                            }}
                        >
                            Pay {bal}
                        </Button>
                    </>
                )}
            </Card>
        </Container> ) }

const CouierIndex = ({walletShowHeader}) => {
    const [tabIndex, setTabIndex] = useState(0);
    const [processStart, setProcessStart] = useState(false);
    const [currentPage , setCurrentPage] = useState('')
    // const [processStart, setProcessStart] = useState(true);
    const [ showSponsorTab,setShowSponsorTab] = useState(false)
    const [paymentComplete, setPaymentComplete] = useState(false);
    const [kycApproved , isKycApproved] = useState(false)
    // const [paymentComplete, setPaymentComplete] = useState(true);
    const [completedSteps, setCompletedSteps] = useState(false);
    const [sponserUser , setSponserUser] = useState({})
    const [user , setUser] = useState({})
    const [opBoardingComplete , setOnBoardingComplete] = useState(false)
    const [loading , setLoading] = useState(true)
    const [pageLoader , setpageLoader] = useState(false)
    
    const handleComplete = () => {
      setCompletedSteps((pre)=>!pre)
        // setCompletedSteps((prev) => new Set([...prev, tabIndex]));
        // setTabIndex((prev) => prev + 1);

    };

    const validateOnboarding = async()=> {
      setpageLoader(true)
        try {
            let res  = await Getprofile()
            if(res?.data?.error === false)
            {
              // setOnBoardingComplete(res?.data?.data?.is_courierOnboarding)
                if(res?.data?.data?.is_courierSingup === false)
                {
                  setTabIndex(0)
                  setCurrentPage('onboarding-page')
                  processStart(false)
                  return
                }

                if(res?.data?.data?.is_courierSingup === true)
                {
                  setProcessStart(true)
                }
                if(res?.data?.data?.is_courierDoc === false )
                  {
                    setTabIndex(1)
                    setCurrentPage('onboarding-page')
                    return
                  }
                  if(res?.data?.data?.is_courierVideo === false )
                    {
                      setTabIndex(2)
                      setCurrentPage('onboarding-page')
                      return
                    }  
                    if(res?.data?.data?.is_courierAgreement === false )
                      {
                        setTabIndex(3)
                        setCurrentPage('onboarding-page')
                        return
                      }    
                  if(res?.data?.data?.is_courierSingup === true && res?.data?.data?.is_courierDoc === true && res?.data?.data?.is_courierVideo === true && res?.data?.data?.is_courierAgreement === true && res?.data?.data?.is_courierOnboarding === false )  
                  {
                    setCurrentPage('onboarding-pending-page')
                    return
                  }

                  if(res?.data?.data?.is_courierSingup === true && res?.data?.data?.is_courierDoc === true && res?.data?.data?.is_courierVideo === true &&  res?.data?.data?.is_courierAgreement === true && res?.data?.data?.is_courierOnboarding === true )  
                    {
                      setCurrentPage('courier-page')
                      setOnBoardingComplete(res?.data?.data?.is_courierOnboarding)
                      // setTabIndex(3)
                      // setCurrentPage('onboarding-page')
                      return
                    }
            }  
        } catch (error) {
            
        }
        finally {
            setLoading(false)
            setpageLoader(false)
        }
    
    }



    
    useEffect(()=>{
        validateOnboarding()
    },[completedSteps])
    
    if(loading)
    {
        return null
    }

   
    // !completedSteps.has(0)
    if ( currentPage === 'courier-page' && opBoardingComplete === true) {

        return (
        <CourierDashboardProvider>
          <CouierDashboard />
        </CourierDashboardProvider>
        )
    }

    return (
      <>
      {pageLoader && <Loader/>}
      <div className="PageHeading"><h1>Courier</h1></div>
      { currentPage === 'onboarding-pending-page' && 
         <CourierKycPending/>
      }
      
      {currentPage === 'onboarding-page' &&
        <Container style={{ marginTop: "2rem" }}>
            
            {!processStart ? (
                <SponsorVerification 
                    setProcessStart={setProcessStart} 
                    setShowSponsorTab={setShowSponsorTab}
                    setSponserUser={setSponserUser}
                    sponserUser={sponserUser}
                    setUser={setUser}
                />
            ) : !paymentComplete ? (
                <PaymentGateWay setPaymentComplete={setPaymentComplete} showSponsorTab={showSponsorTab} />
            ) : (
                <>
                    <Tabs value={tabIndex} onChange={(e, newValue) => setTabIndex(newValue)} centered>
                        <Tab label="Signup Form" disabled={true} />
                        <Tab label="Upload Business Images" disabled={true} />
                        <Tab label="Upload Self Declaration Video" disabled={true} />
                       
                        <Tab label="Agreement" disabled={true} />
                    </Tabs>
                    <Card style={{ padding: "2rem", marginTop: "1rem", boxShadow: "0px 4px 10px rgba(0,0,0,0.1)" }}>
                        {tabIndex === 0 && <SignupForm setpageLoader={setpageLoader} walletShowHeader={walletShowHeader} user={user} onComplete={handleComplete} />}
                        {tabIndex === 1 && <UploadBusinessImages  onComplete={handleComplete} />}
                        {tabIndex === 2 && <UploadBusinessVideo onComplete={handleComplete} />}
                        {tabIndex === 3 && <VerifyDocuments setCurrentPage={setCurrentPage} onComplete={handleComplete} />}
                    </Card>
                </>
            )}
        </Container>
}
        </>
    );
};

export default CouierIndex;