import { useState } from 'react';
import { Box, Typography, Tooltip, IconButton, Avatar } from '@mui/material';
import { PhotoCamera, Delete } from '@mui/icons-material';
// import { cloudImage } from '../api/upload/imageUpload';
import { cloudImage } from '../api/login/Login';
import { genrateUrl } from '../utils/genrateUrl';


const useCourierImageUploadOnboard = () => {
    // const [image, setImage] = useState(null);
    const [imageUrl, setImageUrl] = useState('');
 
    const handleImageChange = async (e) => {
        const file = e.target.files[0];
        const allowedTypes = ['image/jpeg', 'image/png', 'image/gif'];

        if (file && allowedTypes.includes(file.type)) {
            // const previewUrl = URL.createObjectURL(file);
            // setImage(previewUrl);

            try {
                const formData = new FormData();
                formData.append('image', file);
                let res = await cloudImage(formData);
                setTimeout(()=>{
                    setImageUrl(res?.data?.data?.url);
                },750)
               
            } catch (error) {
                console.error('Image upload failed:', error);
            }
        }
    };
   

   
    const handleRemoveImage = () => {
        // setImage(null);
        setImageUrl('');
    };

    

    const ImageUpload = ({ index = 0, title = 'No Image', editBool = true }) => (
        <Box
            sx={{
                position: 'relative',
                width: '100%',
                height: 250,
                borderRadius: 2,
                overflow: 'hidden',
                bgcolor: 'grey.200',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                cursor: 'pointer',
            }}
            onClick={() => !imageUrl && document.getElementById(`image-upload-${index}`)?.click()}
        >
            {imageUrl ? (
                <Avatar
                    src={genrateUrl(imageUrl)}
                    variant="rounded"
                    sx={{ width: '100%', height: '100%' }}
                    onClick={(e) => e.stopPropagation()}
                />
            ) : (
                <Typography color="textSecondary">{title}</Typography>
            )}
            <input
                accept="image/*"
                style={{ display: 'none' }}
                id={`image-upload-${index}`}
                type="file"
                onChange={(e) => handleImageChange(e, index)}
            />
            {!imageUrl && (
                <Tooltip title="Upload Image">
                    <IconButton
                        component="span"
                        onClick={(e) => {
                            e.stopPropagation();
                            document.getElementById(`image-upload-${index}`)?.click();
                        }}
                        sx={{ position: 'absolute', bottom: 8, right: 8, bgcolor: 'white' }}
                    >
                        <PhotoCamera />
                    </IconButton>
                </Tooltip>
            )}
            {imageUrl && editBool && (
                <Tooltip title="Remove Image">
                    <IconButton
                        onClick={(e) => {
                            e.stopPropagation();
                            handleRemoveImage(index);
                        }}
                        sx={{ position: 'absolute', top: 8, right: 8, bgcolor: 'white' }}
                    >
                        <Delete />
                    </IconButton>
                </Tooltip>
            )}
        </Box>
    );

    return { ImageUpload, imageUrl , setImageUrl };
};

export default useCourierImageUploadOnboard;
