import React from 'react';
import { Container, Row, Col, Button, Typography, Box } from '@mui/material';
import { Link } from 'react-router-dom';
import { FaCheckCircle } from 'react-icons/fa';

const CourierKycPending = () => {
  return (
    <Container maxWidth="lg" className="mt-4 d-flex align-items-center justify-content-center">
      <Box
        sx={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          justifyContent: 'center',
          borderRadius: 2,
          boxShadow: 3,
          padding: 4,
          backgroundColor: 'white',
        }}
      >
        <FaCheckCircle className="confirmation-icon mb-4" />
        <Typography variant="h3" fontSize={'20px'} color="var(--bs-nav)" gutterBottom>
          Congratulations!
        </Typography>
        <Typography
          variant="body1"
          color="textSecondary"
          sx={{ textAlign: 'left', marginBottom: 4 }}
          fontSize={'14px'}
        >
          KYC Verification in Progress
          <br />
          Thank you for submitting your KYC documents. Your verification is
          currently under review. Please bear with us for a moment while we
          process your details. You will be notified as soon as your KYC is
          approved via an OTP. In the meantime, please feel free to reach out
          if you have any questions.
        </Typography>
        <Button
          variant="contained"
          size="large"
          color="primary"
          sx={{ textTransform: 'none' ,
            backgroundColor: 'var(--bs-primary1)', 
            color: "white",
          
          }}
        >
          <Link to="/dashboard" style={{ textDecoration: 'none', color: 'white' }}>
            <span style={{color:"#fff"}}>
            Back to Dashboard
            </span>
            
          </Link>
        </Button>
      </Box>
    </Container>
  );
};

export default CourierKycPending;
