import { createContext, useContext, useState } from "react";

const  CourierContext = createContext()   

export const  CourierDashboardProvider = ({children})=> {
      const [warehouseDetails , setWarehouseDetails] = useState(null)
      const [city , setCity] = useState('')
      const [shippingCharges , setShippingCharges] = useState({})
      const [qty, setQty] = useState(['']);
      const [length, setLength] = useState(['']);
      const [width, setWidth] = useState(['']);
      const [height, setHeight] = useState(['']);
         const [formData, setFormData] = useState({
          pincode: "",
          weight: "",
          invoiceValue: "",
          insurance: false,
          paymode :"Prepaid",
          ewayBill :"",
          appointmentDelivery: false,
        });

    return (
        <>
        <CourierContext.Provider value={{city ,setCity, shippingCharges ,setShippingCharges ,formData , setFormData ,height , setHeight , qty , setQty ,length ,setLength , width ,setWidth , warehouseDetails , setWarehouseDetails} }>
            {children}
        </CourierContext.Provider>
        </>
    )
}

export const useCourierDashboard = ()=> useContext(CourierContext)